*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    transition: all 0.3s ease-out;
    font-family: 'Montserrat';
}

.alpha-wrapper{
    height: 100%;
    width: 70rem;
    //padding: 3.5rem;
    //border: 2px solid green;
    background: #52efc0;
 
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    header{
        //animation: slide-in-2 1s  0s 1 forwards;
        width: 100%;
        height: 18rem;
        padding: 1rem;
        display: flex;
        align-items: center;
        //border-radius: 2px solid red;
        overflow: hidden;
        gap: 4rem;
        text-overflow: clip;
        //box-shadow: 0px 2px 21px 2px rgba(0, 0, 0, 0.309);
        #header-img{
            width: 10rem;
            height: 10rem;
            margin-left: 2rem;
            border-radius: 5rem;
            animation: spin-out 1s  0s 1 forwards;
            box-shadow: 2px 0px 1px 1px rgba(255, 255, 255, 0.343);
            cursor: pointer;
            &:hover{
               box-shadow: 2px 0px 1px 1px black;
               width: 11rem;
               height: 11rem;
            }
        }/*
        h1{
            animation-delay: 0.3s;
        }
        h3{
            animation-delay: 0.5s;
        }
        p{
            animation-delay: 0.8s;
        }*/

        &::after{
            content: '';
            width: 100%;
            height: 17rem;
            transform: skewY(-10deg);
            background: rgba($color: rgb(0, 0, 0), $alpha: 1);
            background: #05445E;
            opacity: 1;
            position: absolute;
            top: -7rem;
            left: 0;
            height: 30rem;
            z-index: -1;
            animation: slide-in-2 1s  0s 1 forwards;
        }
    }
}

#name{
    &:hover{
        letter-spacing: 0.5rem;
    }
}

.anim-text{
    &:hover{
        letter-spacing: 0.05rem;
    }
    color: rgb(223, 223, 223);
    padding: 0.2rem;
    //z-index: 5;

    animation-name: slide-in;
    animation-iteration-count: 1;
    //animation-direction: forwards;
    animation-duration: 1s;;
    animation-delay: 0;
    /*animation: name duration timing-function delay iteration-count direction fill-mode;*/
}

@keyframes spin-out {
    from{
        width: 0.1rem;
        height: 0.1rem;
        transform: rotate(-360deg);
    }
    to{
        width: 10rem;
        height: 10rem;
        transform: rotate(0deg);
    }
}

@keyframes slide-in {
    /*0%{
        transform: translateX(-5rem);
        opacity: 0;
    }
    100%{
        transform: translateX(0rem);
        opacity: 1;
        color: black;
    }
    
    0%{
        color: black;
        opacity: 1;
    }*/
    from{
        transform: translateX(-100rem);
        opacity: 0;
    }
    to{
        transform: translateX(0rem);
        opacity: 1;
    }
}

    @keyframes slide-in-2 {
        from{
            transform: translateX(100rem);
            opacity: 0;
            transform: skewY(0deg);
        }
        to{
            transform: translateX(0rem);
            opacity: 1;
            transform: skewY(-10deg);
        }
    }

.name-tag{
    position: relative;
    padding: 2rem;
    width: 60%;
    padding: 1rem;
}

.social-links{
    position: absolute;
    bottom: 2px;
    right: 1rem;
    display: flex;
    flex-direction: row;
    gap: 1rem;
    //extend .anim-text
    animation-name: slide-in;
    animation-iteration-count: 1;
    //animation-direction: forwards;
    animation-duration: 1s;
    color: rgb(0, 0, 0);
    animation-delay: 0;

    a{
        width: 1rem;
        height: 1rem;
        padding: 1rem;
        img{
            width: 2rem;
            height: 2rem;
            &:hover{
                width: 2.5rem;
                height: 2.5rem;
            }
        }
    }
}

.work{
    margin-top: 4rem;
    width: 100%;
    //height: 36rem;
    //border: 2px solid red;
    display: grid;
    place-content: center;
    position: relative;
    ::after{
        content: '';
        transform: skewY(5deg);
        //background: rgb(110, 12, 201);
        background: blue;
        background:#354a7a;
        background: #189ab4;
        position: absolute;

        width: 100%;
        height: 17rem;
        
        top: -7rem;
        left: 0;
        height: 30rem;
        z-index: -5;
    }
    h3{
        text-align: center;
        font-size: 2rem;
        color: whitesmoke;
    }
}

.work-grid{
    width: 100%;
    height: 100%;
    //border: 2px solid white;
    display: grid;
    grid-template-rows: 1fr 1fr;
    grid-template-columns: 1fr 1fr 1fr;
    padding: 3rem;
    column-gap: 3rem;
    row-gap: 2rem;
}

.work-grid-divs{
    background: rgb(0, 0, 0);
    width: 15rem;
    height: 20rem;
    padding: 1rem;
}

