.screenshot-div img {
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.screenshot-div img:hover {
  width: 105%;
  height: 105%;
}

.project-name {
  color: white;
  font-family: "Poppins";
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 0.5rem;
}
.project-name h6 {
  font-size: 1rem;
}
.project-name img {
  width: 1.8rem;
  height: 1.8rem;
  cursor: pointer;
}
.project-name img:hover {
  width: 2rem;
  height: 2rem;
  padding: 0rem;
}

.short-description {
  color: white;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}